.Menu {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50vh;
    z-index: 100;
}

.Button {
    padding: 40px 80px;
    font-size: 2em;
    border-radius: 20px;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.5) 0px 0px 60px;
    cursor: pointer;
}

.GameStatsMenu {
    position: absolute;
    top: 40%;
    list-style-type: none;
    color: rgba(255, 255, 255, 1);
    padding: .5em;
}

.Highscore {
    position: absolute;
    top: 55%;
}

.values {
    margin: .01em;
    padding: .2em;
}

